.app__aboutus{
    position: relative;
}
.app__aboutus-overlay{
    position: absolute;
    inset:0;  /*  new :) */
}
.app__aboutus-overlay img{
    width: 391px;
    height: 415px;
    z-index: 0;
}
.app__aboutus-content{
    width: 100%;
    z-index: 2;
}
.app__aboutus-content_about{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    text-align: right;
}
.app__aboutus-content_knife{
    margin: 2rem 4rem;
}
.app__aboutus-content_knife img{
    height: 910px;
}
.app__aboutus-content_history{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    text-align: left;
}
.app__aboutus-content_about p,
.app__aboutus-content_history p{
    margin: 2rem 0;
    color: var(--color-grey);
} 

@media screen and (min-width:2000px) {
    .app__aboutus-content_knife img{
        height: 1000px;
    }
    .app__aboutus-content_about p,
    .app__aboutus-content_history p {
        margin: 4rem 0;
    }
}
@media screen and (max-width:900px) {
 .app__aboutus-content{
    flex-direction: column;
 }   
 .app__aboutus-content_knife{
    margin: 4rem 0;
 }
}
@media screen and (max-width:650px) {
    .app__aboutus-overlay img{
        width: 80%;
        height: 320px;
    }
}

